body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:rgb(168, 8, 12)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


h1 {
  text-align: center;
  color: aliceblue;
}

.App {
  display: flex;
  justify-content: space-around;
  height: 100vh;
  width: 100%;
  flex-wrap: wrap;
  
}

.cardcontainer {
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 450px;
  background-color: rgb(22, 22, 22);
  box-shadow: 0px 0px 10px #141313;
  margin-top: 20px;
}


.imgcontainer {
  width: 100%;
}


img {
  width: 100%;
  height: 230px;
}


.card-title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;

}

.card-title > h3 {
  font-family:Arial, Helvetica, sans-serif;
  font-size: 1.6rem;
  color: rgb(130, 106, 61)
}

.card-body {
  padding: 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -15px;
}

.card-body> p {
  font-family:Arial, Helvetica, sans-serif;
  font-size: 1.2rem;
  color: rgb(198, 198, 207);
}

.btn {
  text-align: center;
}

button {
 
  background-color: none;
  border: 2px solid rgb(45, 121, 172);
  
}

button > a {
  width: 100%;
  text-decoration: none;
  font-size: 1.2rem;
  margin-top: -5px;
}


